

import { loginConstant as lc } from '../constants/loginConstant';

const userData = {
    user: [],
    loggedIn: false,
    isLoading: false
};

const userReducer = (state = userData, action) => {
    switch (action.type) {
        case lc.SUCCESS: {
            return {
                ...state,
                user: action.payload,
                loggedIn: true,
                isLoading: false
            }
        }
        case lc.LOADING: {
            return {
                ...state,
                isLoading: true
            }
        }
        case lc.LOGOUT:
            return {
                ...state,
                loggedIn: false,
                isLoading: false
            }
        case lc.UPDATE: {
            return {
                ...state,
                loggedIn: true,
                isLoading: false
            }
        }

        default: {
            return state;
        }
    }
}

export default userReducer;