import React, { useState, useContext, useEffect } from "react";
import { AppContext } from './../context/AppContext';
import avatar from "../../assets/product-details/avatar-50.png";
import Love from "../../assets/product-details/love-blue.svg";
import RedLove from "../../assets/heart.svg";
import Share from "../../assets/product-details/share-blue.svg";
import ModalCart from '../ModalCart';
import { useDispatch, useSelector } from "react-redux";
import { updateDataUser } from "../../redux/actions/userActions";
import { toast } from "react-toastify";

const ToastWarning={
  position: toast.POSITION.TOP_CENTER,
  bodyClassName:"text-sm font-white font-med block p-3",
  autoClose: 2000,
  style:{
    borderRadius: '10px',
    backgroundColor:"rgb(242, 48, 90)",
    color:'white'
  }
}
export default function BottomProductInfo({ productDetail }) {
  const {
    seller,
    actualPrice,
    id,
    img
  } = productDetail;
  const dispatch = useDispatch();
  const [quantity, setQuantity] = useState(1);
  const userData = useSelector(state => state.userData);
  const [price] = useState(actualPrice);
  const [total, setTotal] = useState(() => price * quantity);
  const { currencyFormatter } = useContext(AppContext);
  const [isOpen, setIsOpen] = useState(false);
  const [isCart, setIsCart] = useState(false);
  const [isLiked, setIsLiked] = useState(false);
  const increment = () => {
    setQuantity(quantity + 1);
    setTotal(prevTotal => prevTotal + price)
  };

  const decrement = () => {
    setQuantity(quantity - 1);
    setTotal(prevTotal => prevTotal - price)
  };
  const handleCart = (value) => {
    setIsOpen(true)
    setIsCart(value);
    if (value) {
      setIsCart(value);
      let userTemp = { ...userData };
      let cart = userTemp.user.cart && [...userTemp.user.cart];
      let cartData = cart.find((obj) => obj.id === id);
      let cartId = cart.map(function (obj) { return obj.id; }).indexOf(id);
      if (cartData) {
        cartData.qty = cartData.qty + quantity;
        userTemp.user.cart[cartId] = cartData;
      } else {
        let tempCart = {
          id: id,
          qty: quantity
        }
        userTemp.user.cart.push(tempCart);
      }
      dispatch(updateDataUser(userTemp));
    } else {
      setIsCart(value);
      let userTemp = { ...userData };
      let wishlist = userTemp.user.wishlist && [...userTemp.user.wishlist];
      console.log('iiw = ', wishlist);
      let wishlistData = wishlist&&wishlist.find((obj) => obj === id);
      let wishlistId = wishlist&&wishlist.indexOf(id);
      if (wishlistData) {
        if (isLiked) {
          if (wishlistId > -1) {
            wishlist.splice(wishlistId, 1);
          }
        } else {
          wishlist.push(id);
        }
      } else {
        wishlist.push(id);
      }
      userTemp.user.wishlist = wishlist;
      dispatch(updateDataUser(userTemp));
      setIsLiked(!isLiked);
    }
  }

  useEffect(() => {
    // console.log('user data = ', userData);
    try {
      const selectedWishlist = userData.user && userData.user.wishlist.find((obj) => obj === id);
      setIsLiked(selectedWishlist);  
    } catch (error) {
      
    }
    
  }, []);
  return (
    <div className="product-info-bottom">
      {<ModalCart isOpen={isOpen} setOpen={(open) => setIsOpen(open)} isCart={isCart} name={id} isLiked={isLiked} img={img} data={userData.user.cart ? userData.user.cart.find((obj) => obj.id === id) : {}} />}
      <div className="container">
        <div className="left">
          <div className="avatar">
            <img className="avatar-img" src={avatar} alt="avatar-lite" />
          </div>
          <div className="left-text">
            <p className="left-text-shop">{seller}</p>
            <div className="left-text-button">
              <button className="left-text-button-detail">
                Lihat Detail Toko
              </button>
              <button className="left-text-button-chat">Chat Penjual</button>
            </div>
          </div>
        </div>
        <div className="right">
          <div className="right-price">
            <p className="right-price-title">Total</p>
            <p className="right-price-number">Rp. {currencyFormatter(total)}</p>
          </div>
          <div className="right-quantity">
            {quantity > 1 ? (
              <button
                onClick={decrement}
                className="right-quantity-cremental"
              >
                -
              </button>
            ) : (
                <button
                  onClick={decrement}
                  className="right-quantity-cremental disabled"
                  disabled="disabled"
                >
                  -
                </button>
              )}
            <p className="right-quantity-info">{quantity}</p>
            <button onClick={increment} className="right-quantity-cremental">
              +
              </button>
          </div>
          <div className="right-button">
            <button className="right-button-buy">Beli Sekarang</button>
            <button className="right-button-cart" onClick={() => userData.loggedIn ? handleCart(true) : toast.warn('silahkan Login terlebih dahulu', ToastWarning)} >Tambah ke Keranjang</button>
            <button className="right-button-opt" onClick={() => userData.loggedIn ? handleCart(false) : toast.warn('silahkan Login terlebih dahulu', ToastWarning)}>
              <img className="right-button-opt-icon love" src={isLiked && userData.loggedIn ? RedLove : Love} alt="" />
            </button>
            <button className="right-button-opt">
              <img className="right-button-opt-icon" src={Share} alt="" />
            </button>
          </div>
        </div>
      </div>
    </div>
  );
}
