import React, { useState, useEffect } from 'react'
import Arrow from '../assets/arrow-top.svg';

const ScrollToTop = () => {
  const [YPosition, setYPosition] = useState((window.scrollY + window.innerHeight) < (document.body.clientHeight - (document.body.clientHeight - 1500)))

  useEffect(() => {
    window.onscroll = () => setYPosition((window.scrollY + window.innerHeight) < (document.body.clientHeight - (document.body.clientHeight - 1500)));
  }, [])

  const scrollTop = () => {
    window.scrollTo({ top: 0, behavior: 'smooth' })
  }

  return (
    <div className={`scroll-to-top ` + (YPosition ? "scroll-hide" : "scroll-show")} onClick={() => scrollTop()}>
      <img src={Arrow} alt="scroll to top" />
    </div>
  )
}

export default ScrollToTop