import React from 'react';
import dropdownIcon from '../../assets/category-icons/dropdown-icon.svg';

const LocationDropdown = () => {
  return (
    <div className='shipping-dropdown location-dropdown'>
      <p>Jakarta Barat</p>
      <span><img src={dropdownIcon} alt="dropdown icon" /></span>
    </div>
  )
}

export default LocationDropdown
