import { combineReducers } from 'redux';
import highlightReducer from './highlightReducer';
import flashSaleReducer from './flashSaleReducer';
import bestSellingReducer from './bestSellingReducer';
import mostPopularReducer from './mostPopularReducer';
import productsReducer from './productsReducer';
import userReducer from './userReducer';
import { persistReducer } from 'redux-persist';

const authPersistConfig = {
  key: 'userData',
  storage: localStorage,
  whitelist: ['userData']
}

const reducers = combineReducers({
  userData: userReducer,
  products: productsReducer,
  highlight: highlightReducer,
  flashSale: flashSaleReducer,
  bestSelling: bestSellingReducer,
  mostPopularSearches: mostPopularReducer,
})

export default reducers;