import React from 'react'

const Voucher = ({ title, preview }) => {
  return (
    <div className='voucher'>
      <h5 className="voucher-title">{title}</h5>
      <p className="voucher-preview">{preview}</p>
      <div className="voucher-buttons">
        <p className="detail-use">Gunakan</p>
        <p className="detail-voucher">Detail</p>
      </div>
    </div>
  )
}

export default Voucher
