import { result } from 'lodash';
import React, { useEffect, useState } from 'react';
import { loginConstant as lc } from '../constants/loginConstant';

async function getData(){
    let data = await localStorage.getItem('persist:root');
    // console.log(JSON.parse(data));
    data = JSON.parse(data);
    // console.log(data);
    return data.userData
}

export function submitLogin(params) {
    console.log('masuk login ')
    return (dispatch)=> {
        dispatch({
            type: lc.LOADING
        })
        getData().then(function(result) {
            result = JSON.parse(result)
            const res = {
                name: 'Udin Sedunia',
                email: 'udin@gmail.com',
                wishlist: [
                    // 'pink-soap'
                ],
                cart: [
                    // { id: 'pink-soap', qty: 0 }
                ]
            }
    
            dispatch({
                type: lc.SUCCESS,
                payload: result.user.cart&&result.user.cart.length>0?result.user:res
            })
         })
        
        

        setTimeout(
            () => window.location.reload(false), 
            3000
          );
        
    }
}

export function submitLogout() {
    return (dispatch) => {
        dispatch({
            type: lc.LOADING
        })
        dispatch({
            type: lc.LOGOUT
        });
        setTimeout(
            () => window.location.reload(false), 
            3000
          );
    }
}

export function updateDataUser(data) {
    console.group('daya updyae = ', data);
    return (dispatch) => {
        dispatch({
            type: lc.UPDATE,
            payload: data
        })
    }
}