import React, { useState, useEffect } from 'react';
import Breadcrumb from '../components/Breadcrumb';
import Hero from '../components/hero/Hero';
import ProductDescriptionArea from '../components/productDescriptionArea';
import ProductDetails from './../components/productDetails/ProductDetails';
import DetailRecommendation from './../components/detailRecommendation/index';
import OtherShopProducts from '../components/productDetails/OtherShopProducts';
import ProductSearchHistory from '../components/productDetails/ProductSearchHistory';
import BottomProductInfo from '../components/productDetails/BottomProductInfo';
import Footer from '../components/Footer';
import { useParams } from "react-router";
import { useSelector } from "react-redux";
const ProductDetailPage = () => {

  useEffect(() => window.scrollTo(0, 0), []);
  const params = useParams();
  const productId = params.id;
  const productData = useSelector((state) => state.products.items);

  const productDetail = productData.find((obj) => obj.id === productId);
  return (
    <div className="product-detail-page">
      <Hero hide={true} />
      <Breadcrumb />
      <main className="product-detail-content container">
        <div className="product-detail-content-left">
          <ProductDetails productDetail={productDetail} />
          <ProductDescriptionArea />
        </div>
        <div className="product-detail-content-right">
          <DetailRecommendation />
        </div>
      </main>

      <OtherShopProducts />
      <ProductSearchHistory />
        <BottomProductInfo productDetail={productDetail} />
      {/* <BottomProductInfo price={actualPrice} quantity={qty} /> */}
      <Footer />
    </div>
  );
};

export default ProductDetailPage;
