import * as actions from "../actionTypes";
import { initialProducts } from './productsReducer'


const initialFlashSaleState = {
  timerFlashSale: {
    startTimer: "11/26/2020 21:40",
    endTimer: "12/20/2021 00:00",
  },
  items: initialProducts.items.filter(product => product.isOnFlashSale)
};

const flashSaleReducer = (state = initialFlashSaleState, action) => {
  if (action.type === actions.FLASHSALE_TIMER_SETED) {
    return { ...state, timerFlashSale: action.payload };
  }

  return state;
};

export default flashSaleReducer;