import React from 'react';
import StarActive from '../../assets/product-icon/star-yellow.svg';
import Star from '../../assets/product-icon/star-gray.svg';

const StarRates = () => {
  return (
    <div className="star-rates">
      <div className="star-icon-wrapper">
        <img className="icon" src={StarActive} alt="Star Active" />
        <img className="icon" src={StarActive} alt="Star Active" />
        <img className="icon" src={StarActive} alt="Star Active" />
        <img className="icon" src={StarActive} alt="Star Active" />
        <img className="icon" src={Star} alt="Star" />
      </div>
      <p className="rates-number">4/5</p>
    </div>
  );
}


export default StarRates;