import React, { useState, useEffect } from 'react'
import { NavLink } from 'react-router-dom';
import DropdownCategory from './DropdownCategory';
import FormNavbar from './FormNavbar';
import Cart from './Cart';
import Like from './Like';
import { useDispatch, useSelector } from 'react-redux';
import { submitLogin, submitLogout } from '../../redux/actions/userActions';
import 'react-toastify/dist/ReactToastify.css';
import { ToastContainer, toast } from 'react-toastify';
toast.configure();

const ToastWarning={
  position: toast.POSITION.TOP_CENTER,
  autoClose: 2000,
  style:{
    borderRadius: '10px',
    backgroundColor:"rgb(242, 48, 90)",
    color:'white'
  }
}
const ToastSuccess={
  position: toast.POSITION.TOP_CENTER,
  autoClose: 2000,
  style:{
    borderRadius: '10px',
    backgroundColor:"rgb(2, 107, 185)",
    color:'white'
  }
}
const Navbar = () => {
  const [top, setTop] = useState(true);
  const dispatch = useDispatch();

  const scrollHandler = () => {
    let scrolled = document.scrollingElement.scrollTop;
    if (scrolled >= 5) {
      setTop(false)
    } else {
      setTop(true)
    }
  }

  useEffect(() => {
    window.onscroll = () => scrollHandler()

    return () => {
      window.removeEventListener('scroll', scrollHandler())
    }
  }, [top])

  const handleLogin = () => {
    if (dataLogin.loggedIn) {
      dispatch(submitLogout());
      toast.warn('Berhasil Logout', ToastWarning);
    } else{
      dispatch(submitLogin({}));
      toast.warn('Berhasil Login', ToastSuccess)
    }
  }

  const dataLogin = useSelector(state => state.userData);

  useEffect(() => {
    console.log('login success = ', dataLogin)
  }, [dataLogin])

  return (
    <>
      <div className={"navbar-wrapper navbar-sticky " + (!top ? "navbar-shadow" : "")}>
        <div className="container">
          <nav className="navbar">
            <div className="navbar-left">
              <h1 className="logo"><NavLink to='/' onClick={() => { window.scrollTo(0, 0) }}>MoshiMoshiMall</NavLink></h1>
            </div>
            <div className="navbar-center">
              <DropdownCategory />
              <FormNavbar />
            </div>
            <div className="navbar-right">
              <Cart />
              <Like />
              <a onClick={handleLogin} className='login' style={{ backgroundColor: dataLogin.loggedIn ? "#f2305a" : '#026BB9' }}>{dataLogin.loggedIn ? "Keluar" : "Masuk"}</a>
              <a href="/signup" className='signup'>Daftar</a>
            </div>
          </nav>
        </div>
      </div>
      <div style={{ marginTop: "110px" }}></div>
    </>
  )
}

export default Navbar
