import React, { useEffect } from 'react'
import { useHistory } from 'react-router-dom'
import OtherShopProducts from '../components/productDetails/OtherShopProducts';
import Shoe from '../assets/img/shoes/shoe.png';
import Label from '../components/Label';
import ReactDOM from "react-dom";
// import { ModalContext } from "../components/context/modalContext";
import "../styles/_modal-cart.scss";
const ModalCart = ({ isOpen, setOpen, isCart, isLiked, data, img, name }) => {
    const history = useHistory();
    const ref = React.useRef();
    // let { handleModal, modal, setModal } = React.useContext(ModalContext);
    useOnClickOutside(ref, () => outClick());

    const outClick = () => {
        try {
            setOpen(false)
        } catch (error) {
            console.log('err = ', error)
        }

    }
    if (!isOpen) return null
    return ReactDOM.createPortal(
        <div id="myModal" className="modal" style={{ display: 'block' }} >
            <div ref={ref} className="modal-content">
                <div className="modal-header">
                    <span className="close" onClick={() => setOpen(false)} >&times;</span>
                    <h2 className="header-text">{isCart ? "Berhasil Ditambahkan ke Keranjang" : isLiked ? "Barang ditambahkan ke wishlist" : "Barang dihapus dari wishlist"}</h2>
                </div>

                <div className="product-cart">
                    <div className="product-img">
                        <img src={img} style={{height: '80px'}} />
                        <p className="product-text">{name}{' '}{isCart?`(${data.qty} Barang di keranjang)`:""}</p>
                    </div>
                    <div>
                        <Label type='keranjang' content={`Lihat ${isCart ? "Keranjang" : "Wishlist"}`} />
                    </div>
                </div>
            </div>
        </div>,
        document.querySelector("#modal-root")
    )
}
function useOnClickOutside(ref, handler) {
    React.useEffect(
        () => {
            const listener = event => {
                // Do nothing if clicking ref's element or descendent elements
                if (!ref.current || ref.current.contains(event.target)) {
                    return;
                }

                handler(event);
            };

            document.addEventListener('mousedown', listener);
            document.addEventListener('touchstart', listener);

            return () => {
                document.removeEventListener('mousedown', listener);
                document.removeEventListener('touchstart', listener);
            };
        },
        // Add ref and handler to effect dependencies
        // It's worth noting that because passed in handler is a new ...
        // ... function on every render that will cause this effect ...
        // ... callback/cleanup to run every render. It's not a big deal ...
        // ... but to optimize you can wrap handler in useCallback before ...
        // ... passing it into this hook.
        [ref, handler]
    );
}
export default ModalCart
