import React, { useState } from 'react';
import Reviewer from '../../assets/img/reviewer-dummy.png';
import Expand from '../../assets/product-icon/expand-icon.svg';
import threeDot from '../../assets/product-icon/three-dot.svg';
import StarRates from './StarRates';


const FeedbackReviewer = ({ reply, status, discus }) => {
  const [listReply, setListReply] = useState(true);
  const [optionMenus, setOptionMenus] = useState(false);

  return (
    <div className="feedback-reviewer">
      <div className="reviewer">
        <img className="reviewer-img" src={Reviewer} alt={'wawa'} />
        <div className="reviewer-data">
          <p className="reviewer-name">William Leonardo {status && (<span>(Top Reviewer)</span>)}</p>
          <p className="reviewer-date">22 November 2020 {discus && (", 14:30")}</p>
        </div>
        {
          !discus ? (
            <div style={{ marginTop: "-3px" }}>
              <StarRates />
            </div>
          ) : (
              <div className="option" onClick={() => setOptionMenus(!optionMenus)} >
                <img src={threeDot} alt={threeDot} />

                {optionMenus &&
                  (
                    <>
                      <div className="option-menus">
                        <div>laporkan</div>
                      </div>
                      <div className="option-menus-overlay" style={{ display: optionMenus ? "block" : "none" }} onClick={() => setOptionMenus(!optionMenus)}></div>
                    </>
                  )
                }
              </div>
            )
        }

      </div>
      <div className="feedback">
        <p className="feedback-value">“Lorem ipsum dolor sit amet, Lorem ipsum dolor sit amet Lorem ipsum dolor sit am Lorem ipsum dolor sit amet Lorem ipsum dolor sit amet Lorem ipsum dolor sit amet Lorem ipsum dolor sit amet.”</p>
      </div>
      {
        reply && (
          <div className="reply">

            {
              listReply && (
                <>
                  <div className="reply-action">
                    <div onClick={() => setListReply(!listReply)}>Tampilkan 3 balasan</div>
                    <img src={Expand} alt={Expand} onClick={() => setListReply(!listReply)} />
                  </div>
                </>
              )
            }


            <div className="reply-comment">
              <ReplyComment status={true} />
              {
                !listReply && (
                  <>
                    <ReplyComment status={true} />
                    <ReplyComment status={true} />
                    <ReplyComment status={true} />
                  </>
                )
              }
              <ReplyInput />
            </div>
          </div>
        )
      }
    </div>
  );
}

const ReplyComment = ({ status }) => {
  const [replyOptionMenus, setReplyOptionMenus] = useState(false);
  return (
    <div className="feedback-reviewer">
      <div className="reviewer">
        <img className="reviewer-img" src={Reviewer} alt={Reviewer} />
        <div className="reviewer-data">
          <p className="reviewer-name">Harry Potter {status && (<span>(Penjual)</span>)}</p>
          <p className="reviewer-date">22 November 2020, 14:30</p>
        </div>
        <div className="option">
          <img src={threeDot} alt={threeDot} onClick={() => setReplyOptionMenus(!replyOptionMenus)} />

          {replyOptionMenus &&
            (
              <div className="option-menus">
                <div>laporkan</div>
              </div>
            )
          }
        </div>
      </div>
      <div className="feedback">
        <p className="feedback-value">“Lorem ipsum dolor sit amet, Lorem ipsum dolor sit amet Lorem ipsum dolor sit am Lorem ipsum dolor sit amet Lorem ipsum dolor sit amet Lorem ipsum dolor sit amet Lorem ipsum dolor sit amet.”</p>
      </div>
    </div>
  );
}

const ReplyInput = () => {
  const [input, setInput] = useState('')
  return (
    <div className="feedback-reviewer">
      <div className="reviewer">
        <img className="reviewer-img" src={Reviewer} alt={Reviewer} />
        <div className="feedback">
          <form>
            <input className="form-value" placeholder="Tambahkan Komentar Anda" value={input} onChange={(e) => setInput(e.target.value)} />
            <input className="submit" type="submit" value="Kirim" disabled={input === '' ? true : false} style={{ cursor: (input === '' ? "no-drop" : "pointer"), color: (input === '' ? "#707070" : "#026BB9") }} />
          </form>
        </div>
      </div>
    </div>
  );
}


export default FeedbackReviewer;