import React from 'react'
import { useHistory } from 'react-router-dom'

const Breadcrumb = () => {
  const history = useHistory();
  const nav = () => {
    history.push('/')
    window.scrollTo(0, 0)
  }

  return (
    <div className="breadcrumb container">
      <div className="navigation-tab">
        <p className="navigation-tab-inactive" onClick={nav}>Beranda</p>
        <p className="navigation-tab-arrow">{'>'}</p>
        <p className="navigation-tab-inactive">Fashion Pria</p>
        <p className="navigation-tab-arrow">{'>'}</p>
        <p className="navigation-tab-inactive">Sepatu Pria</p>
        <p className="navigation-tab-arrow">{'>'}</p>
        <p className="navigation-tab-active">Sepatu Yeezy 350 v2 Black Red</p>
      </div>
    </div>
  )
}

export default Breadcrumb
