import React, { useState, useContext } from "react";
import { NavLink } from "react-router-dom";
import { AppContext } from './../context/AppContext';

const Card = ({ img, alt, productName, qty, discount, discountPrice, actualPrice, linkTo }) => {
  const [isQtyLow] = useState(() => qty < 6);
  const { currencyFormatter } = useContext(AppContext)

  return (
    <NavLink
      to={`${linkTo}`}
      className="card"
    >
      <div className="card-image">
        <img src={img} alt={alt} />
        <span className="tooltip">{discount} OFF</span>
      </div>
      <div className="card-body">
        <h5 className="card-title">{productName}</h5>
        <p className="stock-text">
          <span style={isQtyLow ? { color: "#FF0000" } : { color: "#026BB9" }}>
            {qty}
          </span>{" "}
          Produk Tersisa
        </p>
        <div className="card-description">
          <p className="discount">Rp. {currencyFormatter(discountPrice)}</p>
          <p className="price">Rp. {currencyFormatter(actualPrice)}</p>
        </div>
      </div>
    </NavLink>
  );
};

export default Card;
