import React, { useState, useEffect, useContext } from 'react';
import { useParams } from "react-router";
import { useSelector } from "react-redux";
import { NavLink } from 'react-router-dom';
import Slider from "react-slick";
import shoe from "../../assets/img/shoes/shoe.png";
import ReactStars from "react-rating-stars-component";
import Voucher from "../Voucher";
import ProductQuality from "../ProductQuality";
import SizeChart from "../productDetails/SizeChart";
import LocationDropdown from "./LocationDropdown";
import ExpeditionDropdown from "./ExpeditionDropdown";
import Label from "../Label";
import dropdownIcon from "../../assets/category-icons/dropdown-icon.svg";
import { AppContext } from './../context/AppContext';

const firstExample = {
  size: 15,
  value: 4,
  edit: false,
  color: "#EAEAEA",
  activeColor: "#FFD800",
};

const settingsSliderTop = {
  dots: false,
  infinite: false,
  draggable: false,
  centerPadding: "0",
  fade: true,
};

const settingsSliderBottom = {
  dots: false,
  infinite: false,
  slidesToShow: 3,
  slidesToScroll: 1,
  focusOnSelect: true,
  centerPadding: "0",
};

const ProductDetails = ({productDetail}) => {
  const { currencyFormatter } = useContext(AppContext);
  const [nav1, setNav1] = useState();
  const [nav2, setNav2] = useState();
  const [sliderTop, setSliderTop] = useState();
  const [sliderBottom, setSliderBottom] = useState();

  useEffect(() => {
    setNav1(sliderTop);
    setNav2(sliderBottom);
  }, [sliderTop, sliderBottom]);

  // const params = useParams();
  // const productId = params.id;

  // const productData = useSelector((state) => state.products.items);

  // const productDetail = productData.find((obj) => obj.id === productId);
  const {
    img,
    productName,
    category,
    discountPrice,
    discount,
    actualPrice,
    review,
    discuss,
    sold,
    size
  } = productDetail;

  return (
    <div className="product-details">
      <NavLink to="/" className="back-to-before">
        <span>
          <img src={dropdownIcon} alt="dropdown icon" />
        </span>
        Kembali ke Sepatu Pria
      </NavLink>
      <div className="details">
        <div className="details-photos">

          <Slider
            {...settingsSliderTop}
            asNavFor={nav2}
            ref={(slider) => setSliderTop(slider)} >
            <img src={img} alt="sepatu kaki" />
            <img src={shoe} alt="sepatu kaki" />
            <img src={shoe} alt="sepatu kaki" />
            <img src={shoe} alt="sepatu kaki" />
          </Slider>

          <div className="slider-highlight">
            <Slider
              {...settingsSliderBottom}
              asNavFor={nav1}
              ref={(slider) => setSliderBottom(slider)}
            >
              <div className="slider-photo">
                <img src={img} alt="sepatu kaki" />
              </div>
              <div className="slider-photo">
                <img src={shoe} alt="sepatu kaki" />
              </div>
              <div className="slider-photo">
                <img src={shoe} alt="sepatu kaki" />
              </div>
              <div className="slider-photo">
                <img src={shoe} alt="sepatu kaki" />
              </div>
            </Slider>
          </div>
        </div>

        <div className="details-description">
          <div className="details-description-category">
            {category || "category"}
          </div>
          <div className="details-description-title">{productName}</div>

          <div className="details-description-information">
            <ReactStars {...firstExample} />
            <p className="feedbacks-count">
              <span>{review}</span> Ulasan
            </p>
            <p className="discussion-count">
              <span>{discuss}</span> Diskusi Produk
            </p>
            <p className="sold-count">
              <span>{sold}</span> Terjual
            </p>
          </div>

          <div className="details-description-old-price">
            <p className="discount-price">Rp. {currencyFormatter(discountPrice)}</p>
            <Label type="discount" content={discount} />
          </div>

          <p className="details-description-actual-price">
            Rp. {currencyFormatter(actualPrice)}
          </p>

          <div className="details-description-store-promo">
            <h5 className="information-title">Promo Toko</h5>
            <div className="voucher-wrapper">
              <Voucher title="Gratis Ongkir" preview="Area Jabodetabek" />
              <Voucher title="Cashback 30rb" preview="Min. Transaksi 100rb" />
            </div>
          </div>

          <div className="details-description-product-quality">
            <h5 className="information-title">Kualitas Barang</h5>
            <div className="quality-wrapper">
              <ProductQuality quality={"Original"} />
              <ProductQuality quality={"Baru"} />
            </div>
          </div>

          {size.isSize === true ? (
            <div className="details-description-sizes">
              <h5 className="information-title">
                Ukuran <span>Rician Ukuran</span>
              </h5>
              <SizeChart sizes={size.readyStock} />
            </div>
          ) : (
              <div></div>
            )}

          <div className="details-description-shipping">
            <h5 className="information-title">Estimasi Pengiriman</h5>
            <p className="shipping-promo">
              Gratis Ongkir min. pembelian Rp. 2.500.000
            </p>
            <div className="shipping-counter">
              <LocationDropdown />
              <ExpeditionDropdown />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ProductDetails;