import React from 'react';
import StarActive from '../../assets/product-icon/star-yellow.svg';

const StarVotes = ({ number, percent, count }) => {
  return (
    <div className="star-5 star-votes-wrapper">
      <p className="votes-number">{number}</p>
      <img className="votes-star" src={StarActive} alt="Star Active" />
      <div className="progress-bar">
        <div className="progress-fill" style={{ width: percent }}></div>
      </div>
      <p className="votes-count">{count}</p>
    </div>
  );
}

export default StarVotes;